.header {
    width: 100%;
    height: 30vh;
    min-height: 250px;
    background: rgb(2, 0, 36);
	background: linear-gradient(
		90deg,
		rgba(2, 0, 36, 1) 0%,
		rgba(255, 67, 0, 1) 0%,
		rgba(1, 13, 1, 1) 100%,
		rgba(0, 0, 0, 1) 100%
    );
    color: #ffff;
    position: relative;
}

h1 {
    text-align: center;
    margin: 0;
    padding: 10px 0;
    font-size: 4rem;
}

.logo {
    width: 150px;
    height: auto;
    position: absolute;
    left: 50%;
    transform: translate(-50%);
    top: calc(100% - 70px);
        
}

.containerUser {
    width: 150px;
    display: flex;
    justify-content: center;
    padding-top: 10px;
    gap: 15px;
    color: white;
}

.icon {
    font-size: 2.3rem;

}

.datos {
    display: flex;
    flex-direction: column;
    overflow: hidden;

}

.datosTitle {
    font-size: 1rem;
    font-weight: 700;
    display: flex;
    justify-content: center;
    
}

.datosUser {
    font-size: 1rem;
    font-weight: 300;
    display: flex;
    justify-content: flex-start;
    
}

