.btn {
    padding: 1.3em 3em;
    font-size: 12px;
    text-transform: uppercase;
    letter-spacing: 2.5px;
    font-weight: 500;
    color: #000;
    background-color: #fff;
    border: none;
    border-radius: 45px;
    box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);
    transition: all 0.3s ease 0s;
    cursor: pointer;
    outline: none;
    position: absolute;
    top: 85%;
    left: 50%;
    transform: translate(-50%);
}

button:hover {
    background-color: rgba(255, 86, 0, 1);
    box-shadow: 0px 15px 20px rgba(255, 67, 0, 1);
    color: #fff;
    
}

.countdown {
    font-size: 4rem;
    font-family: monospace;
    font-weight: 700;
    position: absolute;
    top: 65%;
    left: 50%;
    transform: translate(-50%);
    padding: 20px;
    border-radius: 90px 30px;

}

.modal-background {
    position: absolute;
    right: 0;
    top: 0;
    left: 0;
    bottom: 0;
    display: inline;
    width: 30em;
    height: 33em;
    margin: auto auto;
    background-color: rgba(10, 10, 10, 0.97);
    border: 3px solid rgba(255, 67, 0, 1)
  }
  



